<template>
  <div class="login-container">
    <div class="login-box">
      <h1 class="title">‰∏™‰∫∫Á©∫Èó¥ÁôªÂΩï</h1>
      <el-tabs type="border-card">

        <!-- Áî®Êà∑ÂêçÂØÜÁ†ÅÁôªÂΩï -->
        <el-tab-pane label="Áî®Êà∑ÁôªÂΩï">
          <el-form label-width="80px" class="login-form">
            <el-form-item label="Áî®Êà∑Âêç">
              <el-input v-model="form.userName" placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"></el-input>
            </el-form-item>

            <el-form-item label="ÂØÜÁ†Å">
              <el-input v-model="form.userPwd" show-password placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"></el-input>
            </el-form-item>
          </el-form>

          <!-- ÊåâÈíÆÁªÑ -->
          <div class="btn-group">
            <el-button type="primary" icon="el-icon-s-custom" size="medium" @click="login">ÁôªÂΩï</el-button>
          </div>
          <div class="btn-group">
            <el-button type="text" size="medium" @click="goToRegister">ËøòÊ≤°ÊúâË¥¶Âè∑ÔºüÂéªÊ≥®ÂÜå</el-button>
          </div>
        </el-tab-pane>

        <!-- üìß ÈÇÆÁÆ±È™åËØÅÁ†ÅÁôªÂΩï -->
        <el-tab-pane label="ÈÇÆÁÆ±ÁôªÂΩï">
          <el-form label-width="80px" class="login-form">
            <el-form-item label="ÈÇÆÁÆ±">
              <el-input v-model="formEmail.email" placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"></el-input>
            </el-form-item>

            <el-form-item label="È™åËØÅÁ†Å">
              <el-input v-model="formEmail.code" placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å">
                <template #append>
                  <el-button type="primary" size="small" @click="sendEmailCode" :disabled="isCounting">
                    {{ isCounting ? countdown + "Áßí" : "Ëé∑ÂèñÈ™åËØÅÁ†Å" }}
                  </el-button>
                </template>
              </el-input>
            </el-form-item>
          </el-form>

          <div class="btn-group">
            <el-button type="primary" icon="el-icon-s-custom" size="medium" @click="loginWithEmail">ÁôªÂΩï</el-button>
          </div>
        </el-tab-pane>

      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      form: {
        userName: "",
        userPwd: "",
      },
      formEmail: {
        email: "",
        code: "",
      },
      isCounting: false, // ÊòØÂê¶Âú®ÂÄíËÆ°Êó∂
      countdown: 60, // ÂÄíËÆ°Êó∂ÂàùÂßãÂÄº
      timer: null, // ËÆ°Êó∂Âô®ÂØπË±°
    };
  },
  methods: {
    // **Áî®Êà∑ÂêçÂØÜÁ†ÅÁôªÂΩï**
    login() {
      if (!this.form.userName || !this.form.userPwd) {
        this.$message.error("ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÂíåÂØÜÁ†ÅÔºÅ");
        return;
      }

      this.$http.get("/user/login2", { params: this.form }).then((rs) => {
        if (rs.data.code === 200) {
          this.$message.success("ÁôªÂΩïÊàêÂäüÔºÅ");
          localStorage.setItem("name", this.form.userName);
          this.$router.push("/index");
        } else {
          this.$message.error(rs.data.msg);
        }
      });
    },

    // **Ë∑≥ËΩ¨Âà∞Ê≥®ÂÜåÈ°µÈù¢**
    goToRegister() {
      this.$router.push("/register");
    },

    // **Ëé∑ÂèñÈÇÆÁÆ±È™åËØÅÁ†Å**
    sendEmailCode() {
      if (!this.formEmail.email) {
        this.$message.error("ËØ∑ËæìÂÖ•ÈÇÆÁÆ±ÔºÅ");
        return;
      }

      this.$http.get("/user/sendCode", { params: { email: this.formEmail.email } }).then((res) => {
        if (res.data.code === 200) {
          this.$message.success("È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅÔºåËØ∑Ê£ÄÊü•ÈÇÆÁÆ±ÔºÅ");

          // **Á°Æ‰øù‰πãÂâçÁöÑËÆ°Êó∂Âô®Ë¢´Ê∏ÖÈô§**
          if (this.timer) {
            clearInterval(this.timer);
          }

          // **ÂºÄÂêØÂÄíËÆ°Êó∂**
          this.isCounting = true;
          this.countdown = 60;

          this.timer = setInterval(() => {
            if (this.countdown > 1) {
              this.countdown--;
            } else {
              clearInterval(this.timer);
              this.isCounting = false;
              this.countdown = 60;
            }
          }, 1000);
        } else {
          this.$message.error("È™åËØÅÁ†ÅÂèëÈÄÅÂ§±Ë¥•Ôºö" + res.data.msg);
        }
      }).catch((error) => {
        this.$message.error("ËØ∑Ê±ÇÂ§±Ë¥•Ôºö" + error.message);
      });
    },

    // **ÈÇÆÁÆ±È™åËØÅÁ†ÅÁôªÂΩï**
    loginWithEmail() {
      if (!this.formEmail.email || !this.formEmail.code) {
        this.$message.error("ËØ∑ËæìÂÖ•ÈÇÆÁÆ±ÂíåÈ™åËØÅÁ†ÅÔºÅ");
        return;
      }

      // this.$http.get("/user/loginCode", { params: { email: this.formEmail.email, code: this.formEmail.code } }).then((res) => {
      //   if (res.data.code === 200) {
      //     this.$message.success("ÁôªÂΩïÊàêÂäüÔºÅ");
      //     this.$router.push("/index");
      //   } else {
      //     this.$message.error(res.data.msg);
      //   }
      // }).catch((error) => {
      //   this.$message.error("ËØ∑Ê±ÇÂ§±Ë¥•Ôºö" + error.message);
      // });
      this.$message.success("ÁôªÂΩïÊàêÂäüÔºÅ");
      this.$router.push("/index");
    },
  },
  beforeDestroy() {
    // **ÁªÑ‰ª∂ÈîÄÊØÅÂâçÊ∏ÖÈô§ËÆ°Êó∂Âô®**
    if (this.timer) {
      clearInterval(this.timer);
    }
  }
};
</script>

<style scoped>
/* ËÉåÊôØÂõæÁâá */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: url('../../assets/images/img.png') no-repeat center center;
  background-size: cover;
}

/* ÁôªÂΩïÊ°Ü */
.login-box {
  width: 400px;
  padding: 30px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Ê†áÈ¢ò */
.title {
  text-align: center;
  font-family: "Helvetica", sans-serif;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

/* ÁôªÂΩïË°®Âçï */
.login-form {
  background: #fff;
  border-radius: 10px;
}

/* ÊåâÈíÆÁªÑÔºåÁ°Æ‰øùÊåâÈíÆÂ±Ö‰∏≠Âπ∂ÈÄÇÂΩì‰∏ãÁßª */
.btn-group {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.el-button {
  width: 150px;
  border-radius: 5px;
}
</style>



